.Footer{
    box-sizing: border-box;
    height: 250px;
    padding-right: 5rem;
    color: #2C31CF;
    box-sizing: border-box;
    position: relative;
    display: flex;
    justify-content: space-around;
}
/*******************************/
.Logo-Footer{
    /* display: block; */
    margin-top: -50px;
    /* margin-left: 100px; */
    max-width: 580px;
    float: left;
    /* margin-right: 400px; */
}
.Logo-Footer img{
    max-width: 280px;
}

.SocialNetwork{
    margin-top: -70px;
    margin-left: 5px;
    list-style-type: none;
    /* padding: 0px;
    margin: 0px; */
    /* display: block; */
}
.SocialNetwork li{
    display: inline;
}
.SocialNetwork li a img{
    max-height: 50px;
}
/*******************************/

.Right-footer{
    margin-top: 20px;
    width: 50rem;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}


.List{
    /* display: block; */
    align-content: center;
    padding-inline-start: 0px;
}
.List li{
    margin-top: 9px;
    list-style-type: none;
}
.List li a{
    font-size: 14px;
    color: black;
    text-decoration: none;
}
.List li a:hover{
    text-decoration: underline;
}
/*******************************/
